.series {
    background-repeat: no-repeat !important;
    background-position:center center fixed !important;
    background-size: cover !important;
    height: 450px;
    user-select: none;
    -moz-user-select: none;
}
.series h1 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size:30px;
    font-weight: 700;
}
.series .tv-rating {
    background-color: rgba(230, 210, 170,.25);
    font-family: 'Source Sans Pro', sans-serif;
    color: #fff;
    border-radius: 6px;
    font-weight: 700;
    padding: 3px 6px;
    font-size: 12px;
}
.series .release-date {
    text-transform: uppercase;
    color:#fff;
    font-family: 'Source Sans Pro', sans-serif;
    font-size:14px;
    font-weight: 600;
    letter-spacing: 1px;
}
.series .description  {
    color:#fff;
    font-family: 'Source Sans Pro', sans-serif;
    font-size:15px;
    font-weight: 500;
}
.series .btn-play {
    background-color: #D82148;
    width: 130px;
    padding: 6px 15px;
    color: #fff;
    border: 0;
    font-weight: 700;
    text-transform: uppercase;
    font-family: 'Source Sans Pro', sans-serif;
    letter-spacing: 1px;
    font-size: 14px;
    text-decoration: dashed;
    outline: none;
}
.series .btn-mylist {
    background-color: rgba(255,255,255,.35);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    width: 130px;
    padding: 5px 15px;
    color: #fff;
    font-weight: 700;
    text-transform: uppercase;
    font-family: 'Source Sans Pro', sans-serif;
    letter-spacing: 1px;
    font-size: 14px;
    text-decoration: dashed;
    outline: none;
    border:none;
}
.series .btn-mylist svg {
    color: #fff;
}

.series svg {
    font-size:1.3em;
    margin-right: .2em;
}