/* navigation menu */

.navbar-toggler{
    border:none;
    background:none;
    outline: none;
  }

/* dropdown */
.dropdown-menu {
    padding:0;
    position: absolute;
    left:-40px !important;
    color: #F6FBF4 !important;
    border:0;
    border-radius: 0;
    background-color: rgba(255, 255, 255, 0.6);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
}
.dropdown-item {
    color: #F6FBF4 !important;
    font-size: 13px;
    font-weight: 600;
}
.dropdown-item:hover, .dropdown-item:focus {
    color: #fff;
    background-color: #d81f26 !important;
}
.dropdown-item.active, .dropdown-item:active {
    color: #fff;
    background-color: #d81f26 !important;
    text-decoration: none;
}





/* movie cards */
.card-movie {
    width: 200px;
    height: 280px;
    border-radius: 5px;
    padding: .3rem;
    background: #000;
    position: relative;
    display: flex;
    align-items: flex-end;
    transition: 0.4s ease-out;
    box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.5);
    border:0;
    -webkit-user-select: none;
            user-select: none;
    -moz-user-select: none;
}
.card-movie:hover {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
}
.card-movie:hover:before {
    opacity: 1;
}
.card-movie:hover .description {
    opacity: 1;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}
.card-movie:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    background: rgb(0,0,0);
    background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 100%);
    z-index: 2;
    transition: 0.5s;
    opacity: 0;
}
.card-movie img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 5px;
}
.card-movie h3 {
    font-weight: bold;
    font-size: 15px;
}
.card-movie .description {
    position: relative;
    z-index: 3;
    color: white;
    opacity: 0;
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
    transition: 0.5s;
}
.card-movie .description img {
    margin: 0px;
}
.card-movie .description p {
    color:#fff;
    font-family: 'Source Sans Pro', sans-serif;
    font-size:14px;
    font-weight: 500;
    margin-top: 8px;
}
.card-movie .btn-play {
    width:90px;
    height: 30px;
    background-color: #D82148;
    color: #fff;
    border: 0;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-family: 'Source Sans Pro', sans-serif;
    transition: ease-in-out all .2s;
    font-size: 13px;
}
.card-movie .btn-mylist {
    background-color: rgba(255,255,255,.35);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    width:90px;
    height: 30px;
    color: #fff;
    border: 0;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-family: 'Source Sans Pro', sans-serif;
    transition: ease-in-out all .2s;
    font-size: 13px;
}
.card-movie svg {
    margin:0;
    padding:0;
    width:1.5em;
    height: 1.5em;
}
/* heading title */
.heading-title{
    color:#fff;
