/* navigation menu */

.navbar-toggler{
    border:none;
    background:none;
    outline: none;
  }

/* dropdown */
.dropdown-menu {
    padding:0;
    position: absolute;
    left:-40px !important;
    color: #F6FBF4 !important;
    border:0;
    border-radius: 0;
    background-color: rgba(255, 255, 255, 0.6);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
}
.dropdown-item {
    color: #F6FBF4 !important;
    font-size: 13px;
    font-weight: 600;
}
.dropdown-item:hover, .dropdown-item:focus {
    color: #fff;
    background-color: #d81f26 !important;
}
.dropdown-item.active, .dropdown-item:active {
    color: #fff;
    background-color: #d81f26 !important;
    text-decoration: none;
}



